body {
  margin: 0;
  padding: 0;
  color: white;
  font-family: 'Source Code Pro', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  background-color: #06d1e1;
  background: url(/static/media/palm-trees.be18e693.jpg) no-repeat center center fixed; 
  height: 100%;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
 text-decoration: none;
 color: #3FA7D6;
}

a:hover {
  color: #52c8d4;
}

h1 {
  font-family: 'UnifrakturMaguntia', cursive;
}
.app {
  margin-top: 120px;
  margin-left: 20px;
  margin-right: 20px;
}

.flex-center {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.portrait {
  max-width: 300px;
}

.block {
  display: block;
}

.home-paragraph {
  max-width: 400px;
  line-height: 30px;
  font-size: 20px;
}

.social-container img {
  max-width: 40px;
  margin-top: 20px;
  margin-right: 10px;
  margin-bottom: 100px;
  -webkit-filter: saturate(2);
  filter: saturate(2);
}

.social-container img:hover {
  -webkit-filter: saturate(8);
  filter: saturate(8);
}
